.whead{
    text-align: center;
    padding: 20px;
    background-color: darkgreen;
    color: white;
    font-weight: bold;
    margin-bottom: 20px;
    font-size: 30px;
}

.winput{
    display: block;
    margin: 20px;
    padding: 10px 20px;
    border: none;
    outline: none;
    background-color: darkgreen;
    color: white;
    border-radius: 2px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
}

.wbutton
{
    margin: 20px;
    padding: 10px 20px;
    background-color: darkgreen;
    color: white;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 2px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
}
