.startbutton{
    display: block;
    position: absolute;
    right: 50%;
    top: 50%;
    width: 300px;
    height: 300px;
    transform: translate(50%, -50%);
    border-radius: 50%;
    border: none;
    outline: none;
    cursor: pointer;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    -webkit-transform: translate(50%, -50%);
    -moz-transform: translate(50%, -50%);
    -ms-transform: translate(50%, -50%);
    -o-transform: translate(50%, -50%);
    font-size: 30px;
    font-weight: bold;
    color: darkgreen;
}

.loading{
    display: block;
    position: absolute;
    right: 50%;
    top: 50%;
    color: white;
    font-weight: bold;
    font-size: 20px;
    transform: translate(50%, -50%);
    -webkit-transform: translate(50%, -50%);
    -moz-transform: translate(50%, -50%);
    -ms-transform: translate(50%, -50%);
    -o-transform: translate(50%, -50%);
}

.box{
    width: 50%;
    text-align: center;
    padding: 80px;
    color: white;
    font-weight: bold;
    cursor: pointer;
    transition: 0.5s;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -ms-transition: 0.5s;
    -o-transition: 0.5s;
}

.left{
    background-color: #2196f3;
}
.left:hover{
    background-color: #237dc5;
}

.right{
    background-color: #E91E63;
}
.right:hover{
    background-color: #a51546;
}

.cont{
    display: flex;
    height: 200px;
}

.nextbutton
{
    margin: 20px auto;
    padding: 20px 80px;
    background-color: darkgreen;
    color: white;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 2px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
    font-size: 30px;
    font-weight: bold;
    display: block;

}
@media (max-width:768px) {
    .cont{
        flex-direction: column;
    }
    .box{
        width: 100%;
    }
    .nextbutton{
        margin: 200px auto;
    }
  }

